.gallery-section {
  padding: 60px 0;
}

.search-form {
  width: 600px;
  margin: 0 auto 40px;
  display: flex;
}

.search-input {
  border: 1px solid #303030;
  flex-grow: 1;
  height: 40px;
  padding-left: 15px;
  font-size: 18px;
}

.search-btn, .load-more {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #2ecc71;
  border: none;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 14px;
  transition: background-color .25s linear;
}

.load-more {
  margin: 0 auto;
  display: block;
}

.load-more.is-hidden {
  display: none;
}

.search-btn:hover, .search-btn:focus, .load-more:hover, .load-more:focus {
  background-color: #27ae60;
}

.gallery {
  flex-wrap: wrap;
  margin-top: -30px;
  margin-bottom: 20px;
  margin-left: -30px;
  display: flex;
}

.gallery__item {
  flex-basis: calc(25% - 30px);
  margin-top: 30px;
  margin-left: 30px;
}

.gallery-img {
  max-width: unset;
  object-fit: cover;
  width: 100%;
  height: 100%;
}

